'use server'

import { revalidatePath } from 'next/cache'
import admin from '@/lib/firebaseAdmin' // Import the initialized admin instance

const firestore = admin.firestore()

export async function solicitarTurno(userId, turnoData) {
  if (!userId) {
    return { success: false, error: 'Usuario no autenticado.' }
  }

  const { mascotaId, motivo, fecha, tipo } = turnoData

  if (!mascotaId || !motivo || !fecha || !tipo) {
    return { success: false, error: 'Faltan campos obligatorios.' }
  }

  try {
    const turnosCollection = firestore.collection('turnos')

    const nuevoTurno = {
      ...turnoData,
      clienteId: userId,
      estado: 'pendiente',
      fechaSolicitud: admin.firestore.FieldValue.serverTimestamp(),
    }

    await turnosCollection.add(nuevoTurno)

    revalidatePath('/turnos')
    revalidatePath('/admin/turnos')

    return { success: true }
  } catch (error) {
    console.error('Error al solicitar turno:', error)
    return { success: false, error: 'No se pudo procesar la solicitud.' }
  }
}
